@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.Button {
  @include flex;

  width: 150px;
  height: 60px;
  // left: 1000px;
  // top: 928px;
  background: #e37a27;
  border-radius: 10px;
  font-weight: 700;
  font-size: 20px;
  color: #491707;
  cursor: pointer;

  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.8;
  }
}

@media (max-width: 765px) {
  .Button {
    width: 120px;
    height: 48px;
    font-size: 18px;
  }
}
